import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import Customize from '../views/Customize.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/car/:name?',
    name: 'Customize',
    component: Customize,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
